<template>
  <div class="sheet">
    <div v-show="!isActive">
      <!-- 顶部 -->
      <div class="sheet-top">
        <van-nav-bar title="歌单" left-text left-arrow @click-left="onClickLeft" :fixed="true" />
      </div>

      <div class="sheet-bg"></div>
      <div class="sheet-list">
        <ul>
          <li class="clearfix" v-for="(item,index) in map" :key="index" @click="player(item)">
            <div class="list-img fl">
              <img class="img-auto" :src="item.src" alt />
            </div>
            <div class="list-text fl">
              <h3>{{item.name}}</h3>
              <span>{{item.title}}</span>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="sheet-music" v-show="isActive">
      <div class="sheet-close">
        <van-nav-bar 
        :title="sheettitle"
        @click-left="close" 
        left-arrow 
        />
      </div>
      <div class="song-img">
        <img class="img-auto" :src="songsrc" alt="">
      </div>
     <div class="song-lists">
        <div class="song-list-box" :style='top'>
            <p v-for="(item,index) in songlist" :key="index" :data="item.time">{{item.text}}</p>
             
        </div>
     </div>
      <div class="sheet-hua">
        <div class="hua-box">
          <van-slider v-model="value"  :step="0.1" disabled/>
        </div>
      </div>
    </div>

    <!-- 底部 -->
    <div class="player clearfix">
      <div class="index-left fl">
        <div class="cleaxfix">
          <span class="fl">
            <img src="../indeximg/光碟.png" alt />
          </span>
          <p class="fl">QQ音乐 让生活充满音乐</p>
        </div>
      </div>
      <div class="index-right fr">
        <div class="clearfix">
          <span class="fl" @click="pppp">
            <img src="../indeximg/播放.png" alt />
          </span>
          <span class="fl">
            <img src="../indeximg/音乐列表-01.png" alt />
          </span>
        </div>
      </div>
    </div>

    <audio :src="url" ref="audio" id="audio" @canplay="ready" @timeupdate="onTimeupdate"  >
      <!-- <source/> -->
    </audio>
  </div>
</template>
<script>
//2DE8BD
export default {
  name: "sheet",
  data() {
    return {
      data: "",
      sheetlist: [],
      map: [],
      url: "",
      value: 0,
      isActive: false,
      isplay:false,
      sheettitle:"",
      songsrc:"",
      songlist:[],
      issoActive:false,
      top:{"top":"0px"},
      index:0,
      duration:"",
      num:0
    };
  },
  methods: {
    onClickLeft() {
      this.$router.push("/share");
    },
   
    close() {
      this.isActive = !this.isActive;
    },
    player(item) {
      
      var self=this
      this.songlist=[]
      this.top.top=0
    
      
      this.axios({
        //请求类型
        method: "GET",
// http://www.arthurdon.top:3000/song/url?id=
        //请求地址
        url: "https://music.163.com/song/url?id=" + item.id,

        //请求参数
        params: this.params
      }).then(result => {
     
        this.url = result.data.data[0].url;
        this.$store.commit("player", this.url);
        // console.log("this.url=>", this.url);
      });
       this.axios({
        //请求类型
        method: "GET",

        //请求地址
        url: "https://music.163.com/lyric?id=" + item.id,

        //请求参数
        params: this.params
      }).then(result => {
       
         let songl=result.data.lrc.lyric.split(/[\n]/);
         songl.splice(-1,1)
       for( var i=0; i<songl.length;i++){
         var itemml=songl[i].split(/\]/);
      
         // 当前歌词时间
         var songrc=itemml[0].slice(1);
      
        //  //歌词时间
         var time=songrc.split(/:/);
    
         var min=Number(time[0])*60;
      
         var second=Number(time[1]);
      
         var tt=min+second
         self.songlist.push({time:tt,text:itemml[1]});
      
        
      
       }
       
        

      });
     this.ready()
      this.isActive = !this.isActive;
      // console.log("item==>",item)
      this.sheettitle=item.name;
      this.songsrc=item.src
      this.onTimeupdate()
      // console.log(this.songlist)
      this.num=0;
     
    },
    onTimeupdate(){
      
        // console.log(this.$refs.audio.currentTime)
      //  console.log(Math.floor(this.$refs.audio.currentTime/this.duration*100));
       this.value=(this.$refs.audio.currentTime/this.duration).toFixed(2)*100
     
        for(var i=this.num;i<this.songlist.length;i++){
          var t1=this.songlist[i].time
         
          // console.log(t1)
          var t2=0;
          if(this.songlist[i+1]){
            t2=this.songlist[i+1].time
          }else{
            t2=this.songlist.Max_VALUE
          }
          // console.log('t2',t2)
          if(this.$refs.audio.currentTime>=t1&&this.$refs.audio.currentTime<t2){ 
             this.num=i;
            
            //  console.log(this.num);
            this.index=-35.5*i;
            this.top.top=this.index+"px";
          
          };
        };
        
         
         
     
    },
    pause() {
      this.$refs.audio.pause(); 
    },
    ready() {
      // console.log("dd");
      // this.$refs.audio.play();
      document.getElementById('audio').play();
        // console.log("this.$refs.audio.duration==>",this.$refs.audio.duration);
        this.duration=this.$refs.audio.duration
        this.onTimeupdate()
    

    },
    pppp(){
     
       if(!this.isplay){
          this.pause()
         this.isplay=!this.isplay           
          return
       }
       if(this.isplay){
         this.ready();
          this.isplay=!this.isplay     
       }
    },
    ajax(data,url) {
      this.axios({
        //请求类型
        method: "GET",
        //请求地址
        url:url + data,
        //请求参数
        params: this.params
      }).then(result => {
        // console.log("result.data.subjects ==> ", result.data.playlist.tracks.slice(0,20));
        this.sheetlist = result.data.playlist.tracks.slice(0, 10);
        this.liseer();
      });
    },

    liseer() {
      this.sheetlist.forEach(item => {
        this.map.push({
          id: item.id,
          name: item.name,
          src: item.al.picUrl,
          title: item.al.name
        });
      });
    }
  },
  created() {
  
    this.data = this.$route.query.id;
    this.ajax(this.data, "https://music.163.com/playlist/detail?id=");
  },


};
</script>

<style lang="scss" scoped>
.sheet {
  width: 100%;
}
.sheet-bg {
  width: 100%;
  height: 200px;
  background-color: #3080e8;
}
.sheet-list {
  margin-bottom: 60px;
  ul {
    li {
      width: 100%;
      height: 70px;

      box-sizing: border-box;
      border-bottom: 1px solid #ccc;
      .list-img {
        width: 60px;
        height: 60px;
        margin: 5px 20px;
        .img-auto {
          border-radius: 10px;
        }
      }
      .list-text {
        margin-top: 20px;
        h3 {
        }
        span {
        }
      }
    }
  }
}
.sheet-music {
  position: fixed;
  width: 100%;
  height: 92.5%;
  background: url("../indeximg/timg.jpg") no-repeat center center;
  z-index: 11;
  .van-nav-bar{
  opacity: .9;
}
}
.sheet-hua {
  width: 100%;
  height: 30px;
  position: fixed;
  left: 0;
  top: 604px;
  overflow: hidden;
  z-index: 111;
  .hua-box {
    margin: 11px 0;
    .van-slider--disabled{
      opacity: 1;
    }
  }
}
.song-img{
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin: 0 auto;
  margin-top: 30px;
  .img-auto{
     border-radius: 50%;
  }
}
.song-lists{
  width: 100%;
  height: 340px;
  margin-top: 11px;
  // border: 1px solid #000;
  box-sizing: border-box;
  overflow-y: hidden;
  position: relative;
  .song-list-box{
    width: 100%;
    position: absolute;
    p{
      width: 100%;
      height: 18px;
      line-height: 18px;
      text-align: center;
      font-size: 13px;
      padding: 0;
      margin: 14px 0;
      display: block;
    }
    
  }
}

//底部
.player {
  width: 100%;
  height: 50px;
  background-color: #ffffff;
  position: fixed;
  bottom: 0px;
  z-index: 10;
  border-top: 1px solid #ccc;
  .index-left {
    div {
      span {
        display: block;
        height: 35px;
        width: 35px;
        padding: 10px;
        img {
          width: 100%;
          height: 100%;
        }
      }
      p {
        padding: 8px;
        color: #8a8a8a;
      }
    }
  }
  .index-right {
    div {
      margin-top: 12px;
      margin-right: 20px;
      span {
        display: block;
        height: 30px;
        width: 30px;
        margin: 0 8px;

        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}
</style>